.todo-list {
    margin:auto;
    max-width: 400px;
    background-color: #f8f8f8;
    border-radius: 8px;
    padding: 16px;
  }
  
  .todo-list h1 {
    text-align: center;
    color: #333;
  }
  
  .todo-list input[type="text"] {
    width: 100%;
    padding: 8px;
    border: none;
    border-bottom: 2px solid #ccc;
    margin-bottom: 8px;
    font-size: 16px;
  }
  
  .todo-list button {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 8px 16px;
    margin-right: 8px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .todo-list button:hover {
    background-color: #45a049;
  }
  
  .todo-list ul {
    list-style-type: none;
    padding: 0;
  }
  
  .todo-list li {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }
  
  .todo-list li button {
    background-color: #f44336;
    margin-left: 8px;
  }
  
  .todo-list li button:hover {
    background-color: #e53935;
  }
  
  .todo-list p {
    margin: 0;
    font-size: 16px;
    flex-grow: 1;
  }